.profile {
  display: block;
  position: relative;
  width: 100%;
  max-width: 300px;
  background: #fff;
  border-radius: 8px;
  margin: 0 auto;
  box-shadow: 0px 2px 5px rgba(58, 41, 41, 0.401);
  margin-top: 32px;
  margin-bottom: 32px;
  padding-top: 20px;
}

.description {
  width: 100%;
  margin: 0 auto;
  border-top-left-radius: 5px;
  border-top-left-radius: 5px;
}

.avatar {
  display: block;
  width: 100px;
  margin: 0 auto;
  border-radius: 50%;
  background: #bdb7b7;
}

.name {
  margin-top: 30px;
  margin-bottom: 20px;
  font-weight: 600;
  text-align: center;
  font-size: 22px;
}

.tag {
  margin-top: 0;
  margin-bottom: 5px;
  text-align: center;
  opacity: 0.5;
  font-style: 14px;
  font-weight: 600;
}

.location {
  margin: 0;
  padding-bottom: 20px;
  text-align: center;
  opacity: 0.5;
  font-style: 14px;
  font-weight: 700;
}

.stats {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  background: #dedede;
  border: 1px solid #c6c0c0;
  list-style: none;
  padding-left: 0;
}

.statsItem {
  width: 100px;
}

.label {
  display: block;
  text-align: center;
  margin-bottom: 10px;
  opacity: 0.5px;
  font-style: 14px;
  font-weight: 700;
}

.quantity {
  text-align: center;
  display: block;
  font-style: 14px;
  font-weight: 700;
}

.stats li {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: center;
  width: 33%;
  height: 80px;
  gap: 0px;
  border: 1px solid #c6c0c0;
}

.stats li:last-child {
  border-right: none;
}

.stats li span {
  display: block;
  width: 100%;
  text-align: center;
  color: #354354;
}

@media screen and(max-width: 767px;) {
  .profile {
    width: 300px;
  }
}
