.item {
  display: flex;
  align-items: center;
  gap: 0 10px;
  width: 200px;
  margin: 0 auto 10px;
  padding: 10px 0 10px 10px;
  box-shadow: 5px 5px 4px 4px rgba(0, 0, 0, 0.3);
  border-radius: 14px;
  width: 100%;
  font-size: 22px;
  font-weight: 700;
}

.status {
  width: 20px;
  height: 20px;
  border-radius: 50%;
}

.status.isOnline {
  background-color: green;
}

.status.isOffline {
  background-color: red;
}

.avatar {
  background: #d2e7ff;
  border-radius: 8px;
  box-shadow: inset 2px 2px 2px rgba(58, 79, 105, 0.3);
  display: block;
  height: 68px;
  object-fit: cover;
  overflow: hidden;
  padding: 10px;
  width: 68px;
}

.name {
  color: #3c4f65;
  display: inline-block;
  font-size: 22px;
  font-weight: 700;
  line-height: 1.2;
}
